@font-face {
  font-family: "Alumni Sans";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/alumnisans-semibold.woff2") format("woff2"), url("../fonts/alumnisans-semibold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Alumni Sans";
  font-style: normal;
  font-weight: 800;
  src: url("../fonts/alumnisans-extrabold.woff2") format("woff2"), url("../fonts/alumnisans-extrabold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/robotocondensed-regular.woff2") format("woff2"), url("../fonts/robotocondensed-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/robotocondensed-bold.woff2") format("woff2"), url("../fonts/robotocondensed-bold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/robotocondensed-light.woff2") format("woff2"), url("../fonts/robotocondensed-light.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  min-width: 320px;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 103px;
}
@media (max-width: 1199px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: transparent;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ed0233;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.header {
  padding: 30px 0 51px;
  background: conic-gradient(from 139.4deg at 20.7% 35.74%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
  display: flex;
  color: #ffffff;
}
@media (max-width: 1199px) {
  .header {
    padding: 40px 0 82px;
  }
}

.header__inner {
  display: flex;
  justify-content: space-between;
  gap: 50px;
}
.header__inner a {
  font-weight: 600;
  font-size: 26px;
  line-height: 31px;
  letter-spacing: 0.03em;
  font-family: "Alumni Sans", "Arial", sans-serif;
  display: inline-flex;
  color: #ffffff;
  align-self: flex-start;
}
@media (max-width: 1199px) {
  .header__inner a {
    font-size: 24px;
    line-height: 29px;
  }
}
@media (max-width: 767px) {
  .header__inner a {
    font-size: 20px;
    line-height: 24px;
  }
}

.intro {
  display: flex;
  gap: 5%;
}
.intro h2 {
  grid-row: 2/3;
  grid-column: 2/3;
  font-weight: 600;
  font-size: 92px;
  line-height: 110px;
  letter-spacing: 0.05em;
  font-family: "Alumni Sans", "Arial", sans-serif;
  text-align: right;
  margin: 0;
}
@media (max-width: 1199px) {
  .intro h2 {
    font-size: 72px;
    line-height: 86px;
    letter-spacing: 0.03em;
  }
}
@media (max-width: 767px) {
  .intro h2 {
    font-size: 72px;
    line-height: 86px;
    max-width: 300px;
  }
}
.intro span {
  grid-row: 1/2;
  grid-column: 2/3;
  font-weight: 600;
  font-size: 48px;
  line-height: 58px;
  letter-spacing: 0.03em;
  font-family: "Alumni Sans", "Arial", sans-serif;
  text-align: right;
  padding-right: 40px;
}
@media (max-width: 1199px) {
  .intro span {
    font-size: 34px;
    line-height: 41px;
    padding: 0;
    text-transform: uppercase;
  }
}
@media (max-width: 767px) {
  .intro span {
    font-size: 28px;
    line-height: 34px;
  }
}
.intro a {
  grid-row: 3/4;
  grid-column: 2/3;
}
.intro img {
  width: 36vw;
  max-width: 500px;
  height: 556px;
  object-fit: cover;
  transform: translateY(-20px);
}
@media (max-width: 1199px) {
  .intro img {
    width: 100%;
    height: 100%;
    transform: none;
  }
}

.intro__content-wrapper {
  padding: 98px 0 45px;
  max-width: min-content;
  display: flex;
  flex-direction: column;
}
@media (max-width: 1199px) {
  .intro__content-wrapper {
    padding: 57px 0 10px;
    max-width: 50%;
  }
}
@media (max-width: 767px) {
  .intro__content-wrapper {
    padding: 70px 0 0;
    max-width: 100%;
  }
}

.intro__content-inner {
  display: grid;
  grid-template-columns: minmax(113px, 200px) 1fr;
  row-gap: 25px;
  margin-bottom: 50px;
}
@media (max-width: 1199px) {
  .intro__content-inner {
    row-gap: 0;
    column-gap: 7%;
    margin-bottom: 40px;
  }
}
@media (max-width: 1199px) {
  .intro__content-inner {
    column-gap: 5%;
  }
}

@media (max-width: 1199px) {
  .intro__image-wrapper {
    max-width: 324px;
    max-height: 390px;
  }
}
@media (max-width: 767px) {
  .intro__image-wrapper {
    display: none;
  }
}

.intro__list {
  grid-column: 1/2;
  grid-row: 2/3;
  list-style-type: none;
  text-align: right;
  font-size: 14px;
  line-height: 21px;
  align-self: flex-end;
  padding: 0 0 20px;
  margin: 0;
}
@media (max-width: 767px) {
  .intro__list {
    min-width: 113px;
    transform: translateX(15px);
    margin-right: auto;
  }
}

.intro__link {
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  justify-content: center;
  margin-left: auto;
  text-transform: uppercase;
  padding: 24px 25px;
  border: 4px solid #ffffff;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  min-width: 234px;
}
@media (max-width: 1199px) {
  .intro__link {
    padding: 24px 33px;
    min-width: 183px;
  }
}
@media (max-width: 767px) {
  .intro__link {
    width: 100%;
  }
}

.info {
  padding: 120px 0;
}

.info .container {
  display: flex;
  justify-content: space-between;
  gap: 85px;
}

.info__content {
  max-width: 405px;
  padding: 53px 0 0 67px;
}

.info__media {
  min-width: 660px;
}

.video__btn {
  background: transparent;
}

.video {
  position: relative;
  top: -100px;
  left: -60px;
  margin-right: auto;
  width: 364px;
  height: 228px;
}
.video iframe {
  height: 100%;
  width: 100%;
}

.video picture::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  background: rgba(20, 54, 122, 0.5);
  z-index: 2;
}

.info__video-wrapper {
  display: flex;
  flex-direction: column;
}

.video__btn {
  position: absolute;
  top: 50%;
  left: 50%;
  padding: 0;
  z-index: 3;
  transform: translate(-50px, -50px);
}

.subscription {
  padding: 120px 0;
  background: #f8f9fc;
}
.subscription h2 {
  text-align: center;
}

.subscription__month-list {
  text-align: center;
}

.subscription__ticket-list {
  display: flex;
  justify-content: space-between;
  gap: 40px;
}

.promo {
  padding: 120px 0 106px;
}
.promo p,
.promo a {
  grid-column: 1/3;
}

.promo .container {
  display: flex;
}

.promo__content-wrapper {
  background-color: #ed0233;
  display: grid;
  grid-template-columns: 1fr 1fr;
  max-width: 500px;
  padding: 85px 79px 87px 78px;
}

.trainers {
  padding: 0 0 140px;
}

.trainers .container {
  position: relative;
}

.trainers__list {
  margin: 0;
  padding: 0;
  list-style: none;
  cursor: grab;
  display: flex;
}

.trainers__item {
  min-width: 260px;
  display: block;
}

.swiper {
  max-width: 1160px;
  height: 100%;
  overflow: hidden;
}

.trainers__btn {
  position: absolute;
  top: 326px;
  border: none;
  z-index: 10;
  cursor: pointer;
  background: transparent;
}

.trainers__btn--left {
  left: 40px;
}

.trainers__btn--right {
  right: 40px;
  transform: rotate(180deg);
}

.advantages {
  padding: 97px 0 141px;
  background-color: #f8f9fc;
}
.advantages h2 {
  text-align: center;
}

.advantages__item--equipment {
  grid-column: 2/3;
  grid-row: 1/3;
  background-color: #1c3374;
}

.advantages__list {
  display: grid;
  column-gap: 4%;
  grid-template-columns: minmax(342px, 1fr) minmax(240px, 1fr) minmax(342px, 1fr);
}

.stocks {
  padding: 100px 0 117px;
}
.stocks h2 {
  text-align: center;
}

.stocks__list {
  display: grid;
  column-gap: 40px;
  grid-template-columns: 1fr 1fr;
}

.stocks__item--unlimited {
  display: flex;
  grid-row: 1/3;
  background: #ed0233;
}

.stocks__item--discount {
  grid-column: 2/3;
}

.reviews {
  padding: 0 0 120px;
}
.reviews h2 {
  margin: 0 0 37px;
}

.reviews .container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.reviews__list {
  display: flex;
  padding: 0;
}

.swiper-reviews {
  max-width: 560px;
  height: 100%;
  overflow: hidden;
}

.reviews__item {
  min-width: 100%;
}

.reviews__swiper {
  background: #1c3374;
}

.contacts {
  overflow: hidden;
}

.contacts .container {
  display: flex;
}

.contacts form {
  padding: 56px 170px 70px 0;
  background: #102654;
  position: relative;
}
.contacts form::after {
  content: "";
  z-index: -1;
  top: 0;
  right: 0;
  position: absolute;
  width: 100vw;
  height: 100%;
  background: #102654;
}

.contacts fieldset {
  display: flex;
}

.contacts__address {
  padding: 56px 0 70px 120px;
}

.footer {
  padding: 44px 0;
  background: #030a1a;
}

.footer .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

